.gallery {
  background-image: linear-gradient(
    to bottom,
    var(--green-bg) 20%,
    transparent 20%
  );
}

.swiper {
  height: auto;
  border-radius: 8px;

  @media screen and (min-width: 480px) {
    border-radius: 18px;
    max-width: 450px;
    
  }

  @media screen and (min-width: 768px) {
    border-radius: 18px;
    max-width: 704px;
    
  }
  @media screen and (min-width: 1200px) {
    border-radius: 28px;
    max-width: 1013px;
    
  }
}
